import React, {useState} from "react";

function Output(props){

    const [copyText, setCopyText] = useState("copy");
    const [color, setColor] = useState({backgroundColor:"hsl(180, 66%, 49%)"});
  
    

    function handleClick(event){
      const index = event.target.id;
      const textToCopy = document.getElementsByClassName("shortenUrlToCopy")[index].innerHTML;
        event.target.checked = !event.target.checked;
        
      console.log(index);
        if(event.target.checked === true){
            setCopyText(
                "copied!"
            )
            setColor(
              {backgroundColor: "hsl(260, 8%, 14%)"}
            )
            copyTextFunction(textToCopy);
        } else{
            setCopyText(
                "copy"
            )
            setColor(
              {backgroundColor:"hsl(180, 66%, 49%)"}
            )
        }
console.log(document.getElementsByClassName("shortenUrlToCopy"));
console.log(event);
    }

    function copyTextFunction(text) {
      navigator.clipboard.writeText(text);
    }


    return(
      // <section id="output">
        <div className="container container--output">
          <p href={props.userUrl}>{props.userUrl}</p>
          <a  className="shortenUrlToCopy" href={props.shortenUrl} value={props.shortenUrl}>{props.shortenUrl}</a>
          <input id={props.index} style={color} type="button" className="btn btn__square btn__square__output copy-btn" onClick={handleClick} defaultChecked={false} value={copyText}></input>
        </div>
  
      // </section>
    )
    }
  
export default Output;  