import React, { useState } from 'react';

function Navbar(){

  const [change, setChange] = useState("change");
  


  function addChange(){
    const hamburgerBtn = document.getElementsByClassName("hamburger")[0];

    // const y = hamburgerBtn;
    if(change === ""){
      setChange("change");
    }else{
      setChange("");
    }  
    hamburgerBtn.className = "hamburger " + change;
    console.log(hamburgerBtn);

  }
  

    return(
        <div>
        <section id="navbar">

        <div className="navbar">
          <div className="navbar--box navbar--logo">
            <img src="./images/logo.svg" alt=""/>
          </div>
          
        
          
          
          <div className="navbar--box hamburger-menu">
          <div className="hamburger" onClick={addChange}>
          <div className="bar bar1"></div>
          <div className="bar bar2"></div>
          <div className="bar bar3"></div>
          </div>
          <div className="dropdown">
          <div className="navigation">
            <div className="item">
              <a href="https://www.frontendmentor.io?ref=challenge">Features</a>
            </div>
            <div className="item">
              <a href="https://www.frontendmentor.io?ref=challenge">Pricing</a>
            </div>
            <div className="item">
              <a href="https://www.frontendmentor.io?ref=challenge">Resources</a>
            </div>
            <div className="border"></div>
          </div>
          <div className="login">
            <div className="item">
              <button className="btn">Login</button>
            </div>
            <div className="item">
              <button className="btn">Sign Up</button>
            </div>
            </div>

          </div>
          
          </div>
          
        </div>
        
        </section>
        </div> 

    );
}

export default Navbar;