function Input(props){

  const [input, setInput] = useState("");

  function handleChange(event){
    setInput(event.target.value);
  }

  function handleClic(event){
        props.urlCatch(input);
        event.preventDefault();
        setInput("");
        
    }

console.log(props.style);
  
    return(
        <div>
<section id="input">
  <form action="/" className="shortenURL ">
  <div className="grid-container">
  <div className="item item--a">
  <input style={props.style} className="input user_url" id={props.className} name="user_url" type="text" onChange={handleChange} placeholder="Shorten a link here..." value={input}/>
  </div>
  <div className="item item--b">
  <button target="user_url" className="btn btn__big btn__square btn__square__input" type="submit" value="submit" onClick={handleClic}>Shorten It!</button>
  </div>
  <div className="progress item--c" style={props.progress}>
      <CircularProgress color="primary" className="circular" />
  </div>
    </div>
  </form>
  
</section>
<p className="error" style={props.error}>Please enter a correct URL</p>
</div>
    )
}
  

export default Input;
